* {
  margin: 0;
  padding: 0;
  font-family: 'Poppins';
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #080808;
  color: #fff;
}

.logo {
  position: sticky;
  top: 0;
  
}

#header {
  width: 100%;
  height: 100vh;
  background-image: url("pratyush lap.jpg");
  background-size: cover;
  background-position: center;
}

.container {
  padding: 10px 10%;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.logo {
  width: 250px;
  position: sticky;
  top: 0;

}

nav ul li {
  display: inline-block;
  list-style: none;
  margin: 10px 20px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 25px;
  font-weight: 500;
  position: relative;
}

nav ul li a::after {
  content: '';
  width: 0;
  height: 3px;
  background: #00ffdd;
  position: absolute;
  left: 0;
  bottom: -6px;
  transition: 0.5s;
}

nav ul li a:hover::after {
  width: 100%;
}

.header-text {
  margin-top: 15%;
  font-size: 30px;

}

.header-text h1 {
  font-size: 60px;
  margin-top: 20px;

}

.header-text h1 span {
  color: #00e5ff;

}




/* ----------------------about-------------- */

#about {

  color: #ababab;
}

.row {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.about-col-1 {
  flex-basis: 35%;
  margin: 100px 0;
}

.about-col-1 img {
  margin-top: 35px;
  width: 100%;
  border-radius: 15px;
}

.about-col-2 {
  flex-basis: 60%;

}

.subtitle {
  font-size: 60px;
  font-weight: 600;
  color: white;
  margin-top: 170px;

}

.inf {
  font-size: 25px;
  margin: 10px;
  font-family: 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.tab-titles {
  display: flex;
  margin: 20px 0px 40px;
}

.tab-links {
  margin-right: 50px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  position: relative;
}

.tab-links::after {
  content: '';
  width: 50;
  height: 3px;
  background: #4dff00;
  position: absolute;
  left: 0;
  bottom: -8px;
  transition: 0.5s;
}

.tab-links.active-link::after {
  width: 100%;
}

.tab-contents ul li {
  list-style: none;
  margin: 10px 0;
}

.tab-contents ul li span {
  color: #ffd500;
  font-size: 14px;
}

.tab-contents {
  display: none;
}

.tab-contents.active-tab {
  display: block;
}

/* --------------------services--------- */

#services {
  padding: 30px 0;

}

.services-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 40px;
  margin-top: 50px;
}

.services-list div {
  background: #262626;
  padding: 40px;
  font-size: 18px;
  font-weight: 300;
  border-radius: 10px;
  transition: background 0.5s, transform 0.5s;
}

.services-list div i {
  font-size: 50px;
  margin-bottom: 30px;
}

.services-list div h2 {
  font-size: 35px;
  font-weight: 500;
  margin-bottom: 15px;
}

.services-list div a {
  text-decoration: none;
  color: #fff;
  font-size: 12px;
  margin-top: 20px;
  display: inline-block;
}

.services-list div:hover {
  background: #ff004f;
  transform: translateY(-10px);
}

/* ------------portfolio---------------- */
#portfolio {
  padding: 50px 0;
}

.work-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 40px;
  margin-top: 50px;
}

.work {
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.work img {
  width: 100%;
  border-radius: 10px;
  display: block;
  transition: transform 0.6s;
}

.layer {
  width: 100%;
  height: 0;
  background: linear-gradient(rgba(0, 0, 0, 0.5), #ff004f);
  border-radius: 10px;
  position: absolute;
  left: 0;
  bottom: 0;
  overflow: hidden;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  transition: height 0.5s;
}

.layer h3 {
  font-weight: 500;
  margin-bottom: 20px;
}

.layer a {
  color: #ff004f;
  text-decoration: none;
  font-size: 18px;
  line-height: 60px;
  background: #fff;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  text-align: center;
  margin: 20px auto;
}

.work:hover img {
  transform: scale(1.1);
}

.work:hover .layer {
  height: 100%;
}

.btn {
  display: block;
  margin: 50px auto;
  width: fit-content;
  border: 1px solid #ff004f;
  padding: 14px 50px;
  border-radius: 6px;
  text-decoration: none;
  color: white;
  transition: background 0.5s;

}

.btn:hover {
  background: #ff004f;
  box-shadow: 0 0 5px #ff004f,
      0 0 25px #ff004f,
      0 0 50px #ff004f,
      0 0 200px #ff004f;
  -webkit-box-reflect: below 1px linear-gradient(transparent, rgba(5, 5, 5, 0.242));


}

/* ---------------------contact------------ */
.contact-left {
  flex-basis: 35%;
}

.contact-right {
  flex-basis: 60%;
}

.contact-left p {
  margin-top: 30px;
}

.contact-left p i {
  color: #ff004f;
  margin-right: 15px;
  font-size: 25px;
}

.social-icons {
  margin-top: 30px;
}

.social-icons a {
  text-decoration: none;
  font-size: 30px;
  margin-right: 15px;
  color: #ababab;
  display: inline;
  transition: transform 0.5s;
}

.social-icons a:hover {
  color: #ff004f;
  transform: translateY(-5px);
}

.btn.btn2 {
  display: inline-block;
  background-color: #ff004f;
  border: 2px solid grey;

}

.contact-right {
  width: 100%;
}

form input,
form textarea {
  width: 100%;
  border: 0;
  outline: none;
  background: #262626;
  padding: 15px;
  margin: 15px 0;
  color: #fff;
  font-size: 18px;
  border-radius: 6px;
}

form .btn2 {

  padding: 14px 60px;
  font-size: 18px;
  margin-top: 20px;
  cursor: pointer;

}

.copywrite {
  width: 100%;
  text-align: center;
  padding: 25px 0;
  background: #262626;
  font-weight: 400;
  margin-top: 20px;
}

.copywrite i {
  color: #00e5ff;
}

/* ----------------css for small screen----------- */

nav .fa-solid {
  display: none;
}

@media only screen and (max-width:600px) {
  * {
      margin: 0;
      padding: 0;
  }

  #header {
      background-image: url("mobimg.jpg");
  }

  .header-text {
      margin-top: 125%;
      font-size: 20px;
  }

  .header-text h1 {
      font-size: 30px;

  }

  .header-text h1 span {
      font-size: 30px;
      color: #ffd500;
  }

  nav .fa-solid {
      display: block;
  }

  nav ul {
      background: #ff004f;
      position: fixed;
      top: 0;
      right: -200px;
      width: 150px;
      height: 100vh;
      padding-top: 50px;
      z-index: 2;
      transition: right 0.5s;
  }

  nav ul li {
      display: block;
      margin: 45px;
  }

  nav ul .fa-solid {
      position: absolute;
      top: 25px;
      left: 24px;
      cursor: pointer;
  }

  nav .fa-solid {
      font-size: 30px;
      margin-bottom: 20px;
  }

  .about-col-1,
  .about-col-2 {
      flex-basis: 100%;
  }

  .about-col-1 {
      margin-bottom: 30px;
  }


  .subtitle {
      font-size: 40px;
  }

  .about-col-2 {
      font-size: 14px;
  }

  .tab-links {
      font-size: 16px;
      margin-right: 20px;
  }

  .contact-left,
  .contact-right {
      flex-basis: 100%;
  }

  .about-col-1 {
      margin: -10px 0;
  }
      .about-col-2 {
          margin-top: -100px;
      }
  }